import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';

import { Row, Card, Form, Input, Select, message, Spin } from 'antd';

import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import {
  getStudyField,
  getListStudyField,
  updateStudyField,
  createStudyField,
} from 'services/StudyField';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    id: '',
    code: '',
    labelInEnglish: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

const { Option } = Select;

@Form.create({ name: 'formSpcializeTraining' })
class FormSpecializeTrainingPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      systemId: false,
      initialValue: {},
      loading: false,
      listSelect: [],
    };
  }

  componentDidMount = () => {
    const { match } = this.props;
    this.getListData();
    if (match.params.id) {
      this.setState({ systemId: match.params.id, loading: true });
      getStudyField(match.params.id)
        .then(res => {
          const initialValue = handleInitialValue(res.data);
          this.setState({ initialValue, loading: false });
        })
        .catch(error => {
          console.log('error', error);
          this.setState({ loading: false });
        });
    }
  };

  getListData = page => {
    getListStudyField(page, false, false)
      .then(res => {
        this.setState({
          listSelect: res.data.items,
        });
      })
      .catch(error => {
        console.log('====>', error);
      });
  };

  handleSubmit = e => {
    const { match } = this.props;
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        if (match.params.id) {
          updateStudyField(match.params.id, values)
            .then(() => {
              message.success('Chỉnh sửa hệ đào tạo thành công');
              this.props.history.push('/specializeTraining/list');
            })
            .catch(error => {
              console.log('res', error);
              // message.error(error.response.data.message);
            });
        } else {
          createStudyField(values)
            .then(() => {
              message.success('Thêm hệ đào tạo thành công');
              this.props.history.push('/specializeTraining/list');
            })
            .catch(error => {
              console.log('res', error.response);
              // message.error(error.response.data.message);
            });
        }
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { systemId, listSelect, loading } = this.state;
    return (
      <Card title={systemId ? 'Cập nhật chuyên ngành' : 'Thêm chuyên ngành'}>
        <Spin spinning={loading}>
          <Row>
            <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
              <Form.Item hasFeedback label="Ngành đào tạo" {...formItemLayout}>
                {getFieldDecorator('sfId', {
                  initialValue: initialValue.sfId,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(
                  <Select showSearch style={{ width: '100%' }} placeholder="Chọn ngành đào tạo">
                    {listSelect.length > 0
                      ? listSelect.map(item => <Option key={item.id}>{item.label}</Option>)
                      : null}
                  </Select>
                )}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Mã chuyên ngành" hasFeedback>
                {getFieldDecorator('id', {
                  initialValue: initialValue.id,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Tên chuyên ngành" hasFeedback>
                {getFieldDecorator('code', {
                  initialValue: initialValue.label,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Tên tiếng anh" hasFeedback>
                {getFieldDecorator('labelInEnglish', {
                  initialValue: initialValue.labelInEnglish,
                  // rules: [
                  //   {
                  //     required: true,
                  //     message: 'Vui lòng nhập thông tin!',
                  //   },
                  // ],
                })(<Input />)}
              </Form.Item>
              <Form.Item
                style={{ display: 'none' }}
                {...formItemLayout}
                label="Tên tiếng anh"
                hasFeedback
              >
                {getFieldDecorator('trainTypeId', {
                  initialValue: initialValue.trainTypeId,
                })(<Input />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label=" ">
                <BtnSave htmlType="submit" />
                <BtnCancel onClick={() => this.props.history.goBack()} />
              </Form.Item>
            </Form>
          </Row>
        </Spin>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormSpecializeTrainingPage {...props} />
  </Page>
));



